.activities {
    display: flex;
    justify-content: space-evenly;
    flex-wrap: wrap;
    width: 100%;
}

.activity {
    background: rgb(30, 30, 40);
    border-radius: 1rem;
    padding: 10px;
    margin: 5px;
    display: flex;
    align-items: center;
}

.activity-left {
    width: 40%;
    border: 1px solid white;
}

.activity-left > * {
    margin: 5px;
}

.activity-left > img {
    width: 10rem;
    height: 10rem;
    object-fit: cover;
}

.activity-description {
    width: 60%;
}