header {
    display: flex;
    align-items: center;
    justify-content: center;
    min-height: 100vh;
}

header > img {
    width: 15rem;
    max-width: 40%;
    height: 15rem;
    max-height: 40%;
    object-fit: cover;
    border-radius: 100%;
}

.headerText {
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 10px 0;
    border-top: 5px dotted yellow;
    border-bottom: 5px dotted yellow;
    margin: 5px;
}

.headerText > * {
    margin: 20px;
}

@media screen and (max-width: 740px) {
    header {
        flex-direction: column;
        justify-content: space-around;
    }

    header > img {
        width: 40vmin;
        height: 40vmin;
        max-width: 100%;
        max-height: 100%;
        object-fit: cover;
        border-radius: 100%;
    }
}